'use strict'
import Vue from 'vue'
import main from '../../../src/main'
import http from './http'

let common = {}

// 
function onBridgeReady(appId,timeStamp,nonceStr,_package,signType,paySign){
  WeixinJSBridge.invoke(
    'getBrandWCPayRequest', {
      appId,     //     
      timeStamp, //1970     
      nonceStr, //     
      package:_package,
      signType,//     
      paySign, // 
    },
    function (res) {
      console.log(res);
      alert(res)
      if (res.err_msg == "get_brand_wcpay_request:ok") {
        // ,
        //res.err_msgok
      }else{
        let message = '';
        Toast.fail(message);
      }
    }
  );
}
common.submit = (payMoney) => {
  let that = this;
  let params = {//
    data: {
      groupCode: localStorage.getItem('groupCode'),
      accountNo: localStorage.getItem('userNo'),
      payType:1,//12
      payMoney,//
      feeType:1,
      openId: localStorage.getItem('openId'),
      groupOpenId: "oPOvYt3q-Ac9dK1SsEHZF9DDOZnY"
    }
  }
  that.$api.fetch({
    params,//
    apiUrl: '/myWater/wechatPay',//
  }).then(res => {
    let appId = res.appId;
    let timeStamp = res.timeStamp;
    let nonceStr = res.nonceStr;
    let _package = res.package;
    let signType = res.signType;
    let paySign = res.paySign;
    onBridgeReady(appId,timeStamp,nonceStr,_package,signType,paySign)
  })
}

//
//incode groupCode
//countylist 
//citylist 
//areanum vant
common.companyTree = (incode ,countylist,citylist,areanum)=>{
  for (const key in countylist) {//
    let groupCode = key.slice(4)
    if (incode  == groupCode) {
      areanum = key;
    } else {
      for (const item in citylist) {//
        let code = item.slice(2)
        if (incode  == code) {
          areanum = item;
        }
      }
    }
  }
  return areanum;
}

export default common


