common.companyTree = function (incode, countylist, citylist, areanum) {
  for (var key in countylist) {
    //
    var groupCode = key.slice(4);
    if (incode == groupCode) {
      areanum = key;
    } else {
      for (var item in citylist) {
        //
        var code = item.slice(2);
        if (incode == code) {
          areanum = item;
        }
      }
    }
  }
  return areanum;
};

